.time_pick .prev,
.time_pick .next {
  cursor: pointer;
  padding: 18px;
  width: 28%;
  border: 1px solid #ccc;
  margin: auto;
  background: url(../images/arrow.png) no-repeat;
  border-radius: 5px;
}

.time_pick .prev:hover,
.time_pick .next:hover {
  background-color: #ccc;
}

.time_pick .next {
  background-position: 50% 150%;
}

.time_pick .prev {
  background-position: 50% -50%;
}

.time_pick .time_pick {
  position: relative;
}

.timepicker_wrap {
  padding: 10px;
  border-radius: 5px;
  z-index: 998;
