.btn-toggle {
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.btn-toggle > span {
  position: relative;
  display: inline-block;
  width: 56px;
  height: 32px;
  box-sizing: border-box;
  border-radius: 32px;
  vertical-align: middle;
  background: var(--bg-color3);
  border: 1px solid transparent;
  font-size: 0;
  transition: background 0.3s;
}
.btn-toggle > span:before {
  content: "비활성 상태";
  position: absolute;
  top: 50%;
  left: 4px;
  width: 24px;
  height: 24px;
  margin-top: -12px;
  border-radius: 50%;
  background: var(--bg-color2);
  transition: left 0.3s;
}
.btn-toggle input[type=checkbox]:checked ~ span {
  background: var(--primary);
}
.btn-toggle input[type=checkbox]:checked ~ span:before {
  content: "활성 상태";
  left: 27px;
}
.btn-toggle input[type=checkbox]:focus ~ span {
  border-color: var(--primary);
}
.btn-toggle.size-s > span {
  width: 42px;
  height: 24px;
  border-radius: 24px;
}
.btn-toggle.size-s > span:before {
  content: "비활성 상태";
  width: 18px;
  height: 18px;
  left: 2px;
  margin-top: -9px;
}
.btn-toggle.size-s input[type=checkbox]:checked ~ span:before {
  left: 20px;
}

.btn-down span:before {
  position: relative;
  top: -1px;
  margin-right: 2px;
}

.toggle-show {
  cursor: pointer;
  display: inline-block;
  width: 16px;
  height: 16px;
  font-size: 0;
  vertical-align: middle;
}
.toggle-show span {
  display: block;
  width: 16px;
  height: 16px;
  background: var(--icon-url) no-repeat -32px -216px;
}
.toggle-show input[type=checkbox]:checked ~ span {
  background-position-x: -16px;
}

.btn-item--add:before, .btn-item--del:before {
  content: "";
  display: inline-block;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: var(--icon-url) no-repeat;
  vertical-align: -6px;
}
.btn-item--del {
  font-size: 0;
}
.btn-item--del:before {
  background-color: var(--bg-color1);
  background-position: -142px -48px;
}
.btn-item--add {
  color: var(--primary);
  font-weight: 700;
}
.btn-item--add:before {
  margin-right: var(--content-margin1);
  background-color: var(--primary);
  background-position: -164px -48px;
}
.btn-item--add.type-mr2:before {
  margin-right: var(--content-margin2);
}

.btn-tit-toggle {
  position: relative;
  width: 100%;
  text-align: left;
}
.btn-tit-toggle:after {
  content: "";
  position: absolute;
  top: 0;
  width: 24px;
  height: 24px;
  right: 0;
  background-color: var(--primary);
  -webkit-mask-image: var(--icon-url);
  mask-image: var(--icon-url);
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-position: 0 -24px;
  mask-position: 0 -24px;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.btn-tit-toggle.active:after {
  transform: rotate(180deg);
}

